













import { Component, Prop, Watch, Vue } from 'vue-property-decorator';
import RecommendPlanDetails from './RecommendPlanDetails.vue';
import RecommendPlanHistoryCard from './RecommendPlanHistoryCard.vue';

@Component({
  components: {
    RecommendPlanDetails,
    RecommendPlanHistoryCard
  }
})
export default class RecommendProPlanDetails extends Vue {
  @Prop({type: Object, default: () => ({})}) public readonly options!: {[T: string]: number | string };
  private rank: number | string = 0;
  private type: number | string = 0;
  private order: number | string = 0;
  private proId: number | string = 0;
  private gameId: number | string = 0;
  private dataList: {[T: string]: number | string}[] = [];
  private dataFirst: {[T: string]: number | string} = {};
  
  private callApi() {
    // use params to call api;
    // Assign the list to dataList
    // Assign the first value of dataList to dataFirst
    console.log(this.rank, this.type);
  }

  @Watch('$route')
  private routerChange(route: any) {
    const {rank, type, order, proId, gameId} = route.params;
    this.rank = rank;
    this.type = type;
    this.order = order;
    this.proId = proId;
    this.gameId = gameId;
    this.callApi();
  }
  private mounted() {
    const {rank, type, order, proId, gameId} = this.$route.params;
    this.rank = rank;
    this.type = type;
    this.order = order;
    this.proId = proId;
    this.gameId = gameId;
    this.callApi();
  }

}
